.moviesList{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 30px;
    list-style: none;
    padding: 0;
}

.title{
    color: white;
}

.moviesListItem{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        max-width: 300px;

        background-color: #1A1A1A;
        border-radius: 10px;
        border: 1px solid #262626;
        text-decoration: none;
        padding: 12px 12px 0 12px;
        transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

        &:hover{
            box-shadow: rgba(200, 200, 200, 0.05) 0px 1px 1px 0px, rgba(200, 200, 200, 0.2) 0px 0px 0px 1px;
            transform: scale(101%);
        }

        .img{
            width: 100%;
            border-radius: 10px;
            text-align: center;
        }

        .movieTitle{
            text-align: center;
            font-size: 18px;
            max-width: 300px;
            font-weight: 500;
            color: white;
            margin: 0;
            margin-top: 15px;
            
        }

        .thumbsContainer{
            display: flex;
            justify-content: center;
            gap: 10px;
            padding: 12px 12px;
        
            .thumb{
                display: flex;
                gap: 3px;
                align-items: center;
                font-size: 12px;
                color: #999;
                border: 1px solid #262626;
        
                background-color: rgb(20, 20, 20);
                padding: 8px;
                border-radius: 51px;
            }
        }
}